.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: none; }
  .overlay__bg {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    z-index: 11;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; }
  .overlay__wrapper {
    width: 600px;
    height: 800px;
    background-color: #fff;
    position: absolute;
    z-index: 12;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .overlay__wrapper:after {
    content: '';
    position: absolute;
    top: 0;
    right: -10px;
    transform: translateX(100%);
    background-image: url(../img/sprite-04937654700864724.png);
    background-position: -204px -173px;
    width: 41px;
    height: 43px; }
